import React from 'react'
import './projectContainer.scss'

const ProjectContainer = ({alt}) => {
    if (alt === "left") {
        return <Left />
    } else {
        return <Right />
    }
    
}

const Left = () => (
    <div className="container">
        Left
    </div>
)

const Right = () => (
    <div>
        right
    </div>
)


export default ProjectContainer